<template>
    <div>
        <v-dialog
            max-width="400"
            persistent
            v-model="ipSendingDialog"
        >
            <template v-slot:activator="{ on, attrs }">
                    <v-btn class="primaryBtn mr-2" v-bind="attrs"
                        v-on="on" elevation="0" style="width: auto;">
                        <img src="@/assets/images/export.svg"
                         alt="export" class="mr-1" width="22">
                        <span class="fs-16px">Jonatish</span>
                    </v-btn>
            </template>
            <template>
                <v-card class="aksessuar_turi_add">
                    <div class="aksessuar_turi_add__title" id="ffas">
                        <span>Ip jonatilmoqda</span>
                        <v-btn icon @click="closeDialog()">
                            <img src="@/assets/images/BackButton.svg" alt="">
                        </v-btn>
                    </div>

                    <ValidationObserver ref="formIpSending" v-slot="{ handleSubmit, invalid }">
                        <form @submit.prevent="handleSubmit(onSubmit)">

                            <div class="mt-4">
                                <span class="text-black ml-1">Sana</span>
                                
                                    <v-menu
                                        v-model="dateMenu"
                                        :close-on-content-click="false"
                                        :nudge-right="40"
                                        transition="scale-transition"
                                        offset-y
                                        min-width="auto"
                                    >
                                        <template v-slot:activator="{ on, attrs }">
                                            <ValidationProvider rules="required" v-slot="{ errors }">
                                                <v-text-field
                                                    class="mt-2"
                                                    placeholder="Tanlang"
                                                    v-model="dateFormatted"
                                                    readonly
                                                    outlined
                                                    v-bind="attrs"
                                                    v-on="on"
                                                    @blur="date = parseDate(dateFormatted)"
                                                    :error-messages="errors"
                                                ></v-text-field>
                                            </ValidationProvider>
                                        </template>
                                        <ValidationProvider rules="required" v-slot="{ errors }">
                                        <v-date-picker
                                            color="blue lighten-1"
                                            v-model="date"
                                            @input="dateMenu = false"
                                            locale="ru-ru"
                                            first-day-of-week="1"
                                            :error-messages="errors"
                                        ></v-date-picker>
                                        </ValidationProvider>
                                    </v-menu>
                            </div>

                            <div>
                                <span class="text-black ml-1">Qayerga</span>
                                <v-list class="d-flex">
                                    <div class="radioContent">
                                        <span>Mato toquv</span>
                                        <v-radio-group class="radioGroup" v-model="ip_type">
                                            <v-radio value="mato"></v-radio>
                                        </v-radio-group>
                                    </div>
                                    <div class="radioContent">
                                        <span>Yoqa toquv</span>
                                        <v-radio-group class="radioGroup" v-model="ip_type">
                                            <v-radio value="yoqa"></v-radio>
                                        </v-radio-group>
                                    </div>
                                </v-list>

                                <ValidationProvider rules="required" v-slot="{ errors }">
                                    <v-select :items="factoriesList" item-text="name" 
                                        :error-messages="errors" item-value="id" v-model="factorySelected"
                                        label="Toquvxonani tanlang" outlined class="mt-3" >
                                    </v-select>
                                </ValidationProvider>

                            </div>

                            <div class="aksessuar_turi_add__content">

                                <div class="exists">
                                    <span class="exists__title text-black">Mavjud ipni tanlang</span>
                                    <v-text-field outlined class="exists__input" v-model="inputSearch">
                                        <template v-slot:prepend-inner >
                                            <img src="@/assets/icons/search-normal.svg" class="mb-4 mt-0" alt="">
                                        </template>
                                    </v-text-field>
                                </div>


                                <perfect-scrollbar class="mr-1" @ps-y-reach-end="addItems">
                                    <div class="radioContent mb-5" v-for="(element, index) in existingIps" :key="index">
                                        <span>{{element.product.name}} - #{{element.product.code}} - {{formatPrice(element.amount)}} kg</span>
                                         <ValidationProvider rules="required" v-slot="{ errors }">
                                            <v-radio-group class="radioGroup" v-model="existingIp" 
                                                :error-messages="errors">
                                                <v-radio :value="element.product_id"></v-radio>
                                            </v-radio-group>
                                         </ValidationProvider>
                                    </div>
                                </perfect-scrollbar>


                                <div class="mt-4">
                                    <span class="text-black ml-1">Massa (kg)</span>
                                    <ValidationProvider :rules="{required: true, 
                                        error: [errorStatus, errorMessage, errorPlace, 'amount']}" 
                                        v-slot="{ errors }">

                                        <v-text-field outlined class="mt-2" :error-messages="errors"
                                            onkeypress="return /[0-9,/.]/i.test(event.key)"
                                            @input="formatPrice(amount, 'amount')"
                                            placeholder="KG kiriting" v-model="amount">
                                        </v-text-field>
                                    </ValidationProvider>
                                </div>

                            </div>

                            <v-card-actions class="d-flex justify-space-between">
                                <v-btn
                                    text
                                    class="backBtn"
                                    elevation="0"
                                    @click="closeDialog()"
                                >Bekor qilish
                                </v-btn>
                                <v-btn
                                    class="primaryBtn"
                                    elevation="0"
                                    :loading="loading"
                                    :disabled="invalid"
                                    @click="loader = 'loading'"
                                    type="submit"
                                >Tasdiqlash
                                </v-btn>
                            </v-card-actions>
                        </form>
                    </ValidationObserver>
                </v-card>
            </template>
        </v-dialog>
    </div>
</template>

<script>
import { mapState, mapActions } from "vuex";
import { PerfectScrollbar } from 'vue2-perfect-scrollbar'
import { dateMixin, loaderMixin } from '@/mixins/dateLoaderMixins.js'

export default {
    props: [''],
    mixins: [dateMixin, loaderMixin],
    components: {
        PerfectScrollbar,
    },
    data(){
        return{
            // ============= SECONDARY_DATA =============
            page: 1,
            last_page: 2,
            pageCount: 0,
            ipSendingDialog: false,
            ip_type:'',
            inputSearch:'',
            factoriesList: [],
            existingIps: [],
            factorySelected: '',
            amount:'',
            existingIp: '',
            // ============= SECONDARY_DATA =============



            // ============= PRIMARY_DATA =============
            errorStatus: false,
            errorMessage: '',
            errorPlace: ''
            // ============= PRIMARY_DATA =============
        }
    },
    watch: {
        // ============= ERRORS START =============
        amount(newValue, oldValue){
            if(newValue){
                this.clearErrors()
                let element = this.existingIps.filter((e) => e.product.id == this.existingIp);
                if(newValue.toString().replace(/,/g, '') > element[0].amount){
                    this.errorPlace = 'amount'
                    this.errorStatus = true
                    this.errorMessage = 'Kiritilgan massa miqdori mavjud miqdordan oshib ketdi!'
                    this.$toast.error(this.errorMessage)
                }
            }
        },
        '$store.state.main.mavjud_ips': {
            handler: async function(value) {
                if(value){
                    await this.refresh()
                    this.$store.commit('main/SET_UPDATE_MAVJUD_IPS', false)
                }
            },
            deep: true,
            immediate: true
        },
        // ============= ERRORS END =============






        // ============= OTHER_WATCHERS START =============
        async ip_type(newValue, oldValue){
            this.factorySelected = ''
            await this.fetchFactories();
            if(newValue == 'mato'){
                this.factoriesList = this.factories.filter((e) => e.factory_category.type == 'material')
            }else if(newValue == 'yoqa'){
                this.factoriesList = this.factories.filter((e) => e.factory_category.type == 'collar')
            }
        },

        async inputSearch(newValue, oldValue){
            this.page = 1;
            if(newValue || newValue == ''){
                await this.refresh();
            }
        },
        // ============= OTHER_WATCHERS END =============
    },
    async mounted(){
        this.ip_type = 'mato'
        await this.refresh()
    },
    computed:{
        ...mapState(
            {
                mavjud_ips: state => state.warehouse.mavjud_ips,
                factories: state => state.categories.factories
            }
        ),
    },
    methods:{
        ...mapActions(
            {
                postOutcome: 'warehouse/postOutcome',
                fetchMavjudIps: 'warehouse/fetchMavjudIps',
                fetchFactories: 'categories/fetchFactories'
            }
        ),

        



        // ============= REFRESH START =============
        async refresh(){
            this.page = 1;
            
            clearTimeout(this._timerId)

            this._timerId = setTimeout(async () => {
                await this.fetchMavjudIps({page: this.page, search: this.inputSearch})
                this.page = this.mavjud_ips.current_page;
                this.last_page = this.mavjud_ips.last_page;
                this.pageCount = Math.ceil(this.mavjud_ips.total / 20);

                this.existingIps = this.mavjud_ips.data
            }, 500)
        },
        // ============= REFRESH START =============







        // ============= ADD_ITEMS START =============
        async addItems(){
            if(this.page < this.last_page){
                this.page = this.page + 1;

                clearTimeout(this._timerId)

                this._timerId = setTimeout(async () => {
                    await this.fetchMavjudIps({page: this.page, search: this.inputSearch})
                   
                    this.page = this.mavjud_ips.current_page;
                    this.pageCount = Math.ceil(this.mavjud_ips.total / 20);

                    this.existingIps = this.existingIps.concat(this.mavjud_ips.data);
                }, 500)
            }
        },
        // ============= ADD_ITEMS END =============







        // ============= PRICE_FORMATTER START =============
        formatPrice(value, input) {
            if(value != ''){

                let currency = value.toString().replace(/,/g, '');
                currency = parseFloat(currency)
                currency = new Intl.NumberFormat('en-US', {minimumFractionDigits: 0, maximumFractionDigits: 2}).format(currency);


                if(input == 'amount'){
                    this.amount = currency
                }
                return currency
            }
        },
        // ============= PRICE_FORMATTER END =============








        // ============= CLEARS_AND_CLOSES START =============
        clearInputs(){
            this.factorySelected = ''
            this.amount = ''
            this.date = ''
            this.existingIp = ''
            this.ip_type = 'mato'
            this.clearErrors()
        },
        clearErrors(){
            this.errorStatus = false;
            this.errorMessage = '';
            this.errorPlace = '';
        },
        closeDialog(){
            this.clearInputs();
            // this.$parent.ipAddDialog = false;
            this.ipSendingDialog = false
            this.$refs.formIpSending.reset();
        },
        // ============= CLEARS_AND_CLOSES END =============













        // ============= ON_SUBMIT START =============
        async onSubmit(){
            this.$refs.formIpSending.validate().then(async(success) => {
                if(success){
                    // ============= IF SUCCESS =============


                    // ============= SENDING_IP START =============
                    let ip = await this.postOutcome(
                        { 
                            created_at: this.date,
                            factory_id: parseInt(this.factorySelected),
                            product_id: parseInt(this.existingIp),
                            amount: parseFloat(this.amount.toString().replace(/,/g, '')),
                        })

                    if(ip.response && ip.response.status == 422){
                        if(ip.response.data.errors.amount){
                            this.errorMessage = ip.response.data.errors.amount[0];
                            this.errorPlace = 'amount'
                        }
                        this.errorStatus = true
                        this.$toast.error(this.errorMessage);
                    }else if(ip.response && ip.response.status == 409){
                        if(ip.response.data.message){
                            this.errorMessage = ip.response.data.message;
                        }
                        this.$toast.error(this.errorMessage);
                    }else{
                        let toastTitle = 'Muvaffaqiyatli jo`natildi!'
                        this.$toast.success(toastTitle);
                        this.closeDialog();
                        this.$store.commit('main/SET_UPDATE_AFTER_IP_ADDED', true)
                        await this.refresh()
                        this.$store.commit('main/SET_UPDATE_WAREHOUSE_THREAD_OUTCOME', true)
                        this.$store.commit('main/SET_UPDATE_WAREHOUSE_THREAD_EXISTS', true)
                        this.$store.commit('main/SET_UPDATE_WAREHOUSE_THREAD_INPROCESS', true)

                    }
                    // ============= SENDING_IP END =============



                }else{
                    // ============= IF NO SUCCESS =============
                    this.$toast.error('Xato yuz berdi!');
                }
            })

        }
        // ============= ON_SUBMIT END =============

    }

}
</script>


<style>

</style>
