<template>
    <div class="contentPages warehouse" id="warehouse_ip">
        <v-card elevation="0">
            <div class="tabs">
                <v-tabs
                    v-model="tab"
                    background-color="contentPages"
                    color="black"
                >
                    <v-tabs-slider color="teal lighten-3"></v-tabs-slider>
                    <!-- vuex: searchURL -->
                    <v-tab href="#tab-1" @click="routeNAME = 'warehouse-ip-kirim'">
                        <!-- @click="searchURL = `/product2?search=${searchVALUE}`" -->
                        Kirim
                    </v-tab>
                    <v-tab href="#tab-2" @click="routeNAME = 'warehouse-ip-chiqim'">
                        Chiqim
                    </v-tab>

                    <v-tab href="#tab-3" @click="routeNAME = 'warehouse-ip-mavjud'">
                        Mavjud iplar
                    </v-tab>

                    <v-tab href="#tab-4" @click="routeNAME = 'warehouse-ip-jarayon'">
                        Jarayondagi iplar
                    </v-tab>
                </v-tabs>
                <v-col cols="auto">
                    <div class="d-flex align-center">
                        <IpSending v-if="$can('read', 'warehouse_ip_jonatish')"></IpSending>
                        <!-- <TableFilter :filterMenu="filterMenu"></TableFilter> -->
                        <DatePicker v-if="$can('read', 'date')"></DatePicker>
                    </div>
                </v-col>
            </div>
            <v-divider></v-divider>
            <v-tabs-items v-model="tab" class="contentPages">
                <v-tab-item :value="'tab-1'">
                    <Tab1 :update="updateTab1" v-if="$can('read', 'warehouse_ip_kirim')">></Tab1>
                </v-tab-item>
                <v-tab-item :value="'tab-2'">
                    <Tab2 :update="updateTab2" v-if="$can('read', 'warehouse_ip_chiqim')">></Tab2>
                </v-tab-item>
                <v-tab-item :value="'tab-3'">
                    <Tab3 :update="updateTab3" v-if="$can('read', 'warehouse_ip_mavjud')">></Tab3>
                </v-tab-item>
                <v-tab-item :value="'tab-4'">
                    <Tab4 :update="updateTab4" v-if="$can('read', 'warehouse_ip_jarayondagi')">></Tab4>
                </v-tab-item>
            </v-tabs-items>
        </v-card>
    </div>
</template>

<script>
import {mapState, mapActions} from "vuex";

import IpSending from '@/components/modals/Warehouse/IpSending.vue'
import DatePicker from '@/components/DatePicker.vue'
import Tab1 from './Income.vue'
import Tab2 from './Outcome.vue'
import Tab3 from './Exists.vue'
import Tab4 from './InProcess.vue'
//import TableFilter from '@/components/TableFilter.vue'

export default {
    components:{
        IpSending,
        DatePicker,
        Tab1,
        Tab2,
        Tab3,
        Tab4,
        // TableFilter,
        DatePicker
    },
    data(){
        return{
            tab: '',
            updateTab1: false,
            updateTab2: false,
            updateTab3: false,
            updateTab4: false,
            filterMenu: false,
        }
    },
    watch:{
        ipAdded(newValue, oldValue){
            if(newValue){
                this.updateTab1 = true
                this.updateTab2 = true
                this.updateTab3 = true
                this.updateTab4 = true


                setTimeout(() => {
                    this.updateTab1 = false;
                    this.updateTab2 = false;
                    this.updateTab3 = false;
                    this.updateTab4 = false;
                    this.$store.commit('main/SET_UPDATE_AFTER_IP_ADDED', false)
                }, 1000)
            }
        },
    },
    async mounted(){

    },
    computed:{
        ...mapState(
            {
                ipAdded: state => state.main.ipAdded
            }
        ),
        routeNAME: {
            get: function() {
                return this.$store.state.main.routeNAME
            },
            set: function(newValue) {
                return this.$store.state.main.routeNAME = newValue
            }
        },
    },
    methods:{
        ...mapActions(
            {

            }
        ),
        
        
    }
}
</script>

<style>

</style>
