<script>
import {mapState, mapActions} from "vuex";
import { priceMixin } from '@/mixins/formatter.js'
import NoData from "@/components/base/NoData";
import DeleteModal from '@/components/modals/DeleteModal.vue'

export default {
    props: ['update'],
    mixins: [priceMixin],
    components:{
        DeleteModal,
        NoData
    },
    data(){
        return{
            page: 1,
            pageCount: 0,
            editModal: false,
            deleteModal: false,
            loading: false,
            headers: [
                {text: 'Kod', value: 'code', align: 'start', class: 'table-header'},
                {text: 'Nomi', value: 'title', align: 'center', class: 'table-header'},
                {text: 'Kimga', value: 'whom', align: 'center', class: 'table-header'},
                {text: 'Massa (kg)', value: 'mass', align: 'center', class: 'table-header'},
                // {text: 'Harakatlar', value: 'actions', align: 'center', class: 'table-header'},
            ],
           
            items: [],
            items2: [],
        }
    },
    watch: {
        page: async function (newVal, oldVal) {
            if (newVal) {
                await this.refresh()
            }
        },
        update: async function (newVal, oldVal) {
            if (newVal) {
                await this.refresh()
            }
        },
        '$store.state.main.warehouse_thread_inprocess': {
            handler: async function(value) {
                if(value){
                    await this.refresh()
                    this.$store.commit('main/SET_UPDATE_WAREHOUSE_THREAD_INPROCESS', false)
                }
            },
            deep: true,
            immediate: true
        },
    },
    async mounted(){
        await this.refresh()
    },
    computed:{
        ...mapState(
            {
                jarayondagi_ips: state => state.warehouse.jarayondagi_ips,
                searchValue: state => state.main.searchValue,
                dateFilterValue: state => state.main.dateFilterValue
            }
        )
    },
    methods:{
        ...mapActions(
            {
                fetchJarayondagiIps: 'warehouse/fetchJarayondagiIps'
            }
        ),
        async refresh() {
            this.loading = true
            await this.fetchJarayondagiIps({page: this.page, search: this.searchValue, date: this.dateFilterValue})

            this.page = this.jarayondagi_ips.current_page;
            this.pageCount = Math.ceil(this.jarayondagi_ips.total / 20);
            this.items2 = this.jarayondagi_ips.data.slice(10, this.jarayondagi_ips.data.length);
            this.items = this.jarayondagi_ips.data;


            this.items.forEach((element) => {
                element.title = element.product.name
                element.code = element.product.code
                element.whom = element.factory.name
                element.mass = element.amount
            })

            this.items2.forEach((element) => {
                element.title = element.product.name
                element.code = element.product.code
                element.whom = element.factory.name
                element.mass = element.amount
            })
            this.loading = false
        },
        closeEditModal(){
            this.editModal = false
        },
    }
}
</script>

